import Image from "next/image";
import React from "react";
import Title from "./ui/Title";
import Link from "next/link";
import { MdOutlineArrowRightAlt } from "react-icons/md";

const VoresTureSection = () => {
  const tours = [
    {
      image: "moon-btn.jpg",
      text: "MÃ¥nen",
    },
    {
      image: "mars-btn.jpg",
      text: "Mars",
    },
  ];

  return (
    <>
      <section className="flex flex-wrap justify-center gap-x-10 relative">
        {tours.map((tour) => (
          <figure className="relative top-[-50px] flex justify-center ">
            <Image src={`/${tour.image}`} width={500} height={400} alt={`Billede og knap til turen om ${tour.text}`} />
            <Title className="font-bold text-white absolute bottom-[50px] text-4xl" text={tour.text} />
          </figure>
        ))}
      </section>
      <section className="w-40 mx-auto">
        <Link href={"/ture"}>
          <div className="flex items-center text-center gap-1 justify-center">
            <Title text={"Vores ture"} />
            <MdOutlineArrowRightAlt className="text-4xl" />
          </div>
        </Link>
      </section>
    </>
  );
};

export default VoresTureSection;
